'use client'

import { z } from "zod"
import { Button } from "../ui/Button/Button"
import { FormInput } from "../ui/FormInput/FormInput"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod";
import { user } from "@/api/user"
import { useEffect, useState } from "react"
import Link from "next/link"

// const authFormSchema = z.object({
//         email: z.string().email(),
//         password: z.string(),
//     })

// type AuthFormType = z.infer<typeof authFormSchema>

function AuthForm() {

    // const {register, handleSubmit, formState: { errors }} = useForm<AuthFormType>({
    //     resolver: zodResolver(authFormSchema)
    // })

    // const handleClickAuth = (data: AuthFormType) => {
    //     user(data.email, data.password)
    // }

    const [email, setEmail] = useState<string>('')
    const [password, setPassword] = useState<string>('')
    const [data, setData] = useState(null)

    const handleSubmit = (e: Event, {email, password}: { email: string, password: string }) => {
        e.preventDefault()
        user(email, password)
            .then((res) => {
                setData(res)
            })
            .catch(error => {
                console.log(error)
            })
    }

    useEffect(() => {
        console.log(data)
    }, [data])

    return (
        <>
            <form name="authForm" className="flex flex-col gap-[20px] items-center justify-center border-1 border-sky-300 pt-15 pb-15 pl-10 pr-10 rounded-[20px] shadow-md max-w-fit">
                <h2 className="font-medium text-lg m-0">Авторизация</h2>
                <div className="flex flex-col gap-[12px]">
                    <FormInput placeholder="E-mail" type="text" label="E-mail" value={email} onChange={(e) => setEmail(e.target.value)} />
                    <FormInput placeholder="Пароль" type="password" label="Пароль" value={password} onChange={(e) => setPassword(e.target.value)} />
                </div>
                <Button onClick={(e) => handleSubmit(e, {email, password})} classname={"font-medium bg-blue-500 w-40 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out"} type="submit">
                    Войти
                </Button>
                <Link href={'/register'}>
                    <Button classname="cursor-pointer">
                        Регистрация
                    </Button>
                </Link>
            </form>
        </>
    )
}

export default AuthForm